	module.exports = {"data":{"markdownRemark":{"html":"<section class=\"blog-section\">\n<p>In 2012, my 20th year, I wrote a quick <a href=\"http://blog.prestonrichey.com/post/39331250761\" target=\"_blank\">blog post</a> wrapping up some of my favorite things from what I called a 'jumbled and transitory' year. If 2012 was jumbled, I lack the vocabulary to describe this year. (Jumbled-er?) Exhausting and edifying might be close.</p>\n<p>This year, despite being incredibly difficult, was my most creatively productive. I launched projects, both personal and professional, that I'm proud to talk about: <a href=\"https://www.color-composer.net/\" target=\"_blank\">Color Composer</a>, an interactive installation for the Nelson-Atkins' new <a href=\"https://nelson-atkins.org/the-bloch-galleries/\" target=\"_blank\">Bloch Galleries</a>; <a href=\"https://medium.com/moonshotlab/finding-carmen-3c5e28c92f6b\" target=\"_blank\">Carmen</a>, a chatbot made to help Barkley partners find their way; the Mapkins Machine, an Arduino-powered vending machine that dispenses branded napkins when sent a text; <a href=\"https://rottentrumpatoes.com/\" target=\"_blank\">Rotten Trumpatoes</a>, a silly website inspired by <a href=\"https://twitter.com/PJVogt/status/895824447113842689\" target=\"_blank\">a tweet</a>; <a href=\"https://chrome.google.com/webstore/detail/they-linkedin/cmjockpmecfhkaeadcgneakffkioombe\" target=\"_blank\">They LinkedIn</a>, a silly Chrome extension inspired by <a href=\"https://trackchanges.postlight.com/paul-and-rich-try-to-talk-about-something-else-but-end-up-complaining-about-linkedin-again-c20526cfdb74\" target=\"_blank\">a podcast</a>; this site (still a work in progress), my first made with the very promising <a href=\"https://www.gatsbyjs.org/\" target=\"_blank\">Gatsby.js</a>.</p>\n<p>I also made significant steps towards bettering my health overall: I sought and heeded the aid of mental health professionals; I quit even 'casual' smoking entirely; I flossed more than all my previous years combined (gross, I know, sorry).</p>\n<p>Still, 2017 was emotionally wrenching. I'll gloss over the details because I know this year was exhausting for nearly everyone, maybe save for those who get their news exclusively from Fox. In March my dearly loved childhood dog, <a href=\"https://photos.google.com/share/AF1QipPXh0WAEJVOuE9Ki5Z0c0f4RkimbXjNKEaznuFkCtqEUifLpwd1SEhTE2AErrzVTQ/photo/AF1QipNjSOYCLbhITC-Cszg6-4LZcNeYkUjMWo8BDzkY?key=bkF0Q0FEeXJfcEdvQzgxd0FGTjk0dVZZMnRJN0tn\" target=\"_blank\">Keegan</a>, passed away. I gained, then lost, relationships dear to me. My laundromat, staffed by a pair of lovely women who all but adopted me, who gave me leftover roast and sent me a Christmas card, abruptly closed. I probably drank too much.</p>\n<p>Despite all that, there was plenty of good in 2017. I had the opportunity to travel to places I'd never been with people I love. I was enriched by the work of artists and creators I have the privilege to know. I ate a shit-ton of good food (excuse the expression).</p>\n<p>Without further ado, here's a list, in no particular order, of a few of the things I enjoyed last year. Cheers!</p>\n<ul>\n<li>Pho (raw steak, <a href=\"http://www.iphotower.com/\" target=\"_blank\">to go</a>)</li>\n<li>39th Street Laundromat (RIP)</li>\n<li><a href=\"https://photos.google.com/share/AF1QipOGvNdxp2C0d9uHhl7jDhTzlTjZ2f6TpFtYMmoNzyo21c9lsjtKICmJyjqm_hBNOg/photo/AF1QipN4QX9mcYcovr5mnTUmJfgOod5Y24mUr0YZkpmp?key=Y1N2Q2NodkYzNE9xc1l4VXlxMUI0NjV4RktkeTZR\" target=\"_blank\">SF</a>, <a href=\"https://photos.google.com/share/AF1QipOGvNdxp2C0d9uHhl7jDhTzlTjZ2f6TpFtYMmoNzyo21c9lsjtKICmJyjqm_hBNOg/photo/AF1QipNwifklVW3X_ZEZngJdLRV0GSVFIvlDi5swY2Gs?key=Y1N2Q2NodkYzNE9xc1l4VXlxMUI0NjV4RktkeTZR\" target=\"_blank\">Minneapolis</a>, <a href=\"https://photos.google.com/share/AF1QipOGvNdxp2C0d9uHhl7jDhTzlTjZ2f6TpFtYMmoNzyo21c9lsjtKICmJyjqm_hBNOg/photo/AF1QipPZWeca_azDe0flCnJfMiBilz58tzozeXo1zR0G?key=Y1N2Q2NodkYzNE9xc1l4VXlxMUI0NjV4RktkeTZR\" target=\"_blank\">Boston</a>, <a href=\"https://photos.google.com/share/AF1QipOGvNdxp2C0d9uHhl7jDhTzlTjZ2f6TpFtYMmoNzyo21c9lsjtKICmJyjqm_hBNOg/photo/AF1QipN41NdRdvc3dto2Qbop7GIyS4U35bXY0By-e9v6?key=Y1N2Q2NodkYzNE9xc1l4VXlxMUI0NjV4RktkeTZR\" target=\"_blank\">P-town</a>, Wichita, &#x26; Weston with Tanya; <a href=\"https://photos.google.com/share/AF1QipOjNdE6Xk3yaemAgc2gqfd8BI13LVGOu5FmLM0SMLI9kzBpj5H4Bs-ILO9VbNif2A/photo/AF1QipMtdLC3_OY1mBNz47fB4GGg7niFx-sgaHk2OOiD?key=LTA1dnVCUktRbFBoMXFySTVpTTJYbzlaZ0NoOEhB\" target=\"_blank\">Portland</a> &#x26; <a href=\"https://photos.google.com/share/AF1QipOjNdE6Xk3yaemAgc2gqfd8BI13LVGOu5FmLM0SMLI9kzBpj5H4Bs-ILO9VbNif2A/photo/AF1QipPuo9zO6pYVSfnInT9XMG4fLHRT5nZIhibjlmBc?key=LTA1dnVCUktRbFBoMXFySTVpTTJYbzlaZ0NoOEhB\" target=\"_blank\">Manzanitas</a> with Brooks; <a href=\"https://photos.google.com/share/AF1QipOjNdE6Xk3yaemAgc2gqfd8BI13LVGOu5FmLM0SMLI9kzBpj5H4Bs-ILO9VbNif2A/photo/AF1QipOsfqftaOuDBwW5EpDaZjUJ2mD61Okl9smqpjEx?key=LTA1dnVCUktRbFBoMXFySTVpTTJYbzlaZ0NoOEhB\" target=\"_blank\">Seattle</a> &#x26; <a href=\"https://photos.google.com/share/AF1QipOjNdE6Xk3yaemAgc2gqfd8BI13LVGOu5FmLM0SMLI9kzBpj5H4Bs-ILO9VbNif2A/photo/AF1QipNGj2yeRYZafydTo9ZRPrtG0rSXXV2iNZDOrLaC?key=LTA1dnVCUktRbFBoMXFySTVpTTJYbzlaZ0NoOEhB\">Vancouver</a> with Anna; Durham &#x26; <a href=\"https://photos.google.com/share/AF1QipOGvNdxp2C0d9uHhl7jDhTzlTjZ2f6TpFtYMmoNzyo21c9lsjtKICmJyjqm_hBNOg/photo/AF1QipPAa7wC90Nc0NgYebmYmAZWSmMxYJN3LVrieAQa?key=Y1N2Q2NodkYzNE9xc1l4VXlxMUI0NjV4RktkeTZR\" target=\"_blank\">Virginia Beach</a> with the family</li>\n<li><a href=\"https://itunes.apple.com/us/app/hq-live-trivia-game-show/id1232278996?mt=8\" target=\"_blank\">HQ</a></li>\n<li><a href=\"https://www.youtube.com/watch?v=rtW1S5EbHgU\" target=\"_blank\">Harmony of Difference</a> by Kamasi Washington</li>\n<li>This emoji: ðŸ¤ </li>\n<li>Zach Lieberman's <a href=\"https://www.instagram.com/zach.lieberman/\" target=\"_blank\">Instagram</a></li>\n<li><a href=\"http://www.decisionproblem.com/paperclips/index2.html\" target=\"_blank\">Universal Paperclips</a></li>\n<li>VR on the HTC Vive (Google Earth, SUPERHOT, <a href=\"http://store.steampowered.com/app/518580/Accounting/\" target=\"_blank\">Accounting</a>)</li>\n<li>Ethelâ€™s Creamy Lobster Roll at <a href=\"http://row34.com/\" target=\"_blank\">Row 34</a> in Boston</li>\n<li><a href=\"https://www.npr.org/sections/13.7/2017/03/10/519587414/the-power-of-40-speakers-in-a-room\" target=\"_blank\">40 Part Motet</a> at the Nelson-Atkins</li>\n<li>Getting a <a href=\"https://www.leesa.com/\" target=\"_blank\">Leesa</a> mattress</li>\n<li>Podcasts: <a href=\"https://stownpodcast.org/\" target=\"_blank\">S-Town</a>, <a href=\"https://gimletmedia.com/reply-all/\" target=\"_blank\">Reply All</a>, <a href=\"https://soundcloud.com/bodega-sushi\" target=\"_blank\">Bodega Boys</a>, <a href=\"https://overcast.fm/itunes1219395487/stoner\" target=\"_blank\">Stoner</a></li>\n<li><a href=\"https://www.youtube.com/channel/UCJHA_jMfCvEnv-3kRjTCQXw\" target=\"_blank\">Binging With Babish</a></li>\n<li>Radiohead at Sprint Center with Patrick, Aytchie, Tanya</li>\n<li><a href=\"https://www.thehairpin.com/2016/10/ask-a-swole-woman-overnight-oats/\" target=\"_blank\">Overnight Swoalts</a></li>\n<li>Launching weather balloons during the solar eclipse in Beatrice, NE</li>\n<li>Eyeo Festival (particularly, talks from <a href=\"https://vimeo.com/232544904\" target=\"_blank\">Jenny Odell</a>, <a href=\"https://vimeo.com/232545219\" target=\"_blank\">Robin Sloan</a>, &#x26; <a href=\"https://vimeo.com/232544884\" target=\"_blank\">Gene Kogan</a>)</li>\n<li>The arugula salad at <a href=\"http://westportcafeandbar.com/\" target=\"_blank\">Westport Cafe and Bar</a></li>\n<li><a href=\"https://www.sbnation.com/a/17776-football\" target=\"_blank\">17776</a>, by Jon Bois</li>\n<li><a href=\"https://www.amazon.com/Thompson-Tee-Sweat-Bamboo-Medium/dp/B00OL11K6W\" target=\"_blank\">Thompson Tees</a></li>\n</ul>\n</section>","frontmatter":{"title":"</2017>"}}},"pathContext":{"slug":"/blog/2017/"}}

